<template>
  <section>
    <v-card
      color="transparent"
      dark
      shaped
      flat
      style="padding: 10px;"
      width="90%"
      height="88%"
      class="mt-12"
    >
      <v-row justify="center" class="pt-12">
        <v-col cols="5">
          <v-card color="transparent" dark elevation="0">
            <v-card-title
              class="justify-center display-1 text-uppercase font-weight-thin"
              :class="{
                'animate__animated animate__fadeIn animate__slower':
                  activeSection === 3
              }"
              >Kontaktskjema</v-card-title
            >
            <v-form
              ref="form"
              v-model="valid"
              lazy-validation
              class="text-right"
            >
              <v-text-field
                label="E-post"
                v-model="name"
                :rules="fieldRules"
                prepend-icon="mdi-account-question"
              ></v-text-field>
              <v-text-field
                label="Emne"
                v-model="subject"
                :rules="fieldRules"
                prepend-icon="mdi-text-subject"
              ></v-text-field>
              <v-textarea
                label="Innhold"
                v-model="content"
                :rules="fieldRules"
                rows="6"
                no-resize
                prepend-icon="mdi-message-text"
                outlined
              ></v-textarea>
              <v-btn
                :disabled="!valid"
                @click="sendMail"
                color="transparent"
                elevation="0"
              >
                Send
                <v-icon right>mdi-email-send</v-icon>
              </v-btn>
            </v-form>
          </v-card>
        </v-col>
        <v-col cols="1" class="text-center">
          <v-divider vertical color="white" />
        </v-col>
        <v-col cols="5">
          <v-card color="transparent" dark width="65%" elevation="0">
            <v-card-title
              class="justify-center display-1 text-uppercase font-weight-thin"
              :class="{
                'animate__animated animate__fadeIn animate__slower':
                  activeSection === 3
              }"
              >Sosiale Medier</v-card-title
            >
            <a
              :href="item.link"
              v-for="(item, idx) in items"
              :key="idx"
              target="_blank"
            >
              <v-list-item link class="my-5">
                <v-list-item-avatar
                  :class="{
                    'animate__animated animate__fadeIn animate__slower':
                      activeSection === 3
                  }"
                >
                  <v-icon>{{ item.icon }}</v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>{{ item.name }}</v-list-item-title>
                  <v-list-item-subtitle>{{ item.link }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </a>
          </v-card>
        </v-col>
      </v-row>
    </v-card>
  </section>
</template>

<script>
import axios from "axios";

export default {
  name: "Contact",
  props: ["activeSection"],
  data() {
    return {
      valid: false,
      fieldRules: [v => !!v || "Dette feiltet er obligatorisk"],
      name: "",
      subject: "",
      content: "",
      items: [
        {
          name: "LinkedIn",
          icon: "mdi-linkedin",
          link: "https://www.linkedin.com/in/shank4r"
        },
        {
          name: "GitHub",
          icon: "mdi-github",
          link: "https://www.github.com/Shank4r"
        },
        {
          name: "Instagram",
          icon: "mdi-instagram",
          link: "https://www.instagram.com/5hank4r"
        },
        {
          name: "Facebook",
          icon: "mdi-facebook",
          link: "https://www.facebook.com/Shank4r"
        }
      ]
    };
  },
  methods: {
    sendMail() {
      let form = new FormData();
      form.append("name", this.name);
      form.append("subject", this.subject);
      form.append("content", this.content);

      axios.post("/sendMail", form).then(resp => {
        if (resp.status === 200) {
          this.$refs.form.reset();
        }
      });
    }
  }
};
</script>

<style scoped>
section {
  background-image: url("../assets/wallpaper2.jpg");
  background-size: cover;
}
</style>
