<script>
import { HorizontalBar } from "vue-chartjs";
export default {
  name: "egenskap-chart",
  extends: HorizontalBar,
  props: ["data"],
  mounted() {
    this.renderChart(
      {
        labels: this.data.labels,
        datasets: [
          {
            data: this.data.values,
            barPercentage: 0.7,
            backgroundColor: ["#ef476f", "#ffd166", "#06d6a0", "#118ab2"]
          }
        ]
      },
      {
        scales: {
          yAxes: [
            {
              ticks: {
                fontColor: "white"
              }
            }
          ],
          xAxes: [
            {
              ticks: {
                max: 5,
                stepSize: 1,
                beginAtZero: true,
                fontColor: "white"
              },
              scaleLabel: {
                display: true,
                labelString: this.data.xLabel,
                fontColor: "white"
              }
            }
          ]
        },
        legend: {
          display: false
        },
        title: {
          display: true,
          text: this.data.title,
          fontColor: "white"
        }
      }
    );
  }
};
</script>

