import Vue from "vue";
import VueRouter from "vue-router";
import Main from "@/components/Main.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/:part1/:part2?",
    name: "Main",
    component: Main
  },
  {
    path: "*",
    redirect: "/home"
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

const myroutes = ["home", "about", "apps", "contact"];

router.beforeEach((to, from, next) => {
  if (myroutes.includes(to.params.part1)) {
    next();
  } else {
    next({ path: "/home" });
  }
});

export default router;
