<template>
  <div>
    <v-app-bar app color="transparent" elevation="0" dark>
      <v-btn
        v-for="(btn, index) in nav_btn"
        @click="scrollToSection(index)"
        :key="index"
        text
        :to="btn.link"
        class="ma-1"
        >{{ btn.name }}</v-btn
      >
    </v-app-bar>
    <Home class="fullpage" :activeSection="activeSection" />
    <About class="fullpage" :activeSection="activeSection" />
    <Apps class="fullpage" />
    <Contact class="fullpage" :activeSection="activeSection" />

    <float_btn
      :move-down="moveTop"
      :move-up="moveUp"
      :activeSection="activeSection"
    />

    <myfooter v-if="activeSection === 3" />
  </div>
</template>

<script>
import Home from "@/components/Home";
import About from "@/components/About";
import Apps from "@/components/Apps";
import Contact from "@/components/Contact";
import float_btn from "@/components/float_btn";
import myfooter from "@/components/myfooter";

export default {
  name: "Main",
  components: {
    Home,
    About,
    Apps,
    Contact,
    float_btn,
    myfooter
  },
  data() {
    return {
      inMove: false,
      activeSection: 0,
      offsets: [],
      nav_btn: [
        { name: "Hjem", link: "/home", sublink: "" },
        { name: "Om meg", link: "/about", sublink: "/education" },
        { name: "Prosjekter", link: "/apps", sublink: "/maze" },
        { name: "Kontakt", link: "/contact", sublink: "" }
      ]
    };
  },
  methods: {
    calculateSectionOffsets() {
      let sections = document.getElementsByTagName("section");
      let length = sections.length;

      for (let i = 0; i < length; i++) {
        let sectionOffset = sections[i].offsetTop;
        this.offsets.push(sectionOffset);
      }
    },
    handleScroll: function(e) {
    },
    handleMouseWheel: function(e) {
      if (e.wheelDelta < 30 && !this.inMove) {
        this.moveUp();
      } else if (e.wheelDelta > 30 && !this.inMove) {
        this.moveDown();
      }
      e.preventDefault();
      return false;
    },
    handleMouseWheelDOM: function(e) {
      if (e.detail > 0 && !this.inMove) {
        this.moveUp();
      } else if (e.detail < 0 && !this.inMove) {
        this.moveDown();
      }

      return false;
    },
    moveDown() {
      if (this.activeSection - 1 >= 0) {
        this.inMove = true;
        this.activeSection--;
        this.scrollToSection(this.activeSection, true);
      }

      //if (this.activeSection < 0) this.activeSection = this.offsets.length - 1;
    },
    moveUp() {
      if (this.activeSection + 1 < this.offsets.length) {
        this.inMove = true;
        this.activeSection++;
        this.scrollToSection(this.activeSection, true);
      }

      //if (this.activeSection > this.offsets.length - 1) this.activeSection = 0;
    },
    moveTop() {
      this.inMove = true;
      this.activeSection = 0;
      this.scrollToSection(this.activeSection, true);
    },
    scrollToSection(id, force = false) {
      if (this.inMove && !force) return false;

      let tempPath = null;
      if (id === 1) {
        tempPath = "/about/education";
      } else if (id === 2) {
        tempPath = "/apps/maze";
      }

      const routerLink = this.nav_btn[id].link;
      if (tempPath !== null) {
        this.$router.push(tempPath).catch(() => {});
      } else {
        this.$router.push(routerLink).catch(() => {});
      }

      this.activeSection = id;
      this.inMove = true;

      document
        .getElementsByTagName("section")
        [id].scrollIntoView({ behavior: "smooth" });

      setTimeout(() => {
        this.inMove = false;
      }, 400);
    }
  },
  mounted() {
    this.calculateSectionOffsets();

    window.addEventListener("scroll", this.handleScroll);
    window.addEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefox
    window.addEventListener("mousewheel", this.handleMouseWheel, {
      passive: false
    }); // Other browsers

    this.inMove = true;
    this.scrollToSection(
      this.nav_btn.findIndex(
        obj => obj.link === "/" + this.$route.params.part1
      ),
      true
    );
  },
  destroyed() {
    window.removeEventListener("mousewheel", this.handleMouseWheel, {
      passive: false
    }); // Other browsers
    window.removeEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefox
  }
};
</script>

<style scoped></style>
