<template>
  <v-layout row class="fab-container">
    <v-btn
      text
      @click="moveUp"
      color="transparent"
      v-if="!isAtBottom"
      class="animate__animated animate__bounce animate__slow"
    >
      <v-icon color="white">
        mdi-chevron-down
      </v-icon>
    </v-btn>
    <v-btn text @click="moveDown" color="transparent" v-else>
      <v-icon color="white">
        mdi-chevron-up
      </v-icon>
    </v-btn>
  </v-layout>
</template>

<script>
export default {
  name: "float_btn",
  props: ["moveUp", "moveDown", "activeSection"],
  computed: {
    isAtBottom() {
      return this.activeSection === 3;
    },
    isAtTop() {
      return this.activeSection === 0;
    }
  }
};
</script>

<style scoped>
.fab-container {
  position: fixed;
  bottom: 5%;
  left: 49%;
  z-index: 2;
}
</style>
